<template>
<div class="container" v-loading.body="loading" element-loading-text="拼命加载中">
		<el-row >
		  <el-col :span="4">
				已创建客户总数&nbsp;&nbsp;<i style="color:#409EFF;font-size:18px;">{{createdTotal}}个</i>
			</el-col>
		  <el-col :span="4">
				客户信息总字段数&nbsp;&nbsp;<i style="color:#409EFF;font-size:18px;">{{fieldTotal}}个</i>
			</el-col>
		</el-row>

		<el-row >
			<el-col :span="14">
				<el-tabs v-model="activeType" type="card" @tab-click="handleClick" style="margin-top:20px;">
					<el-tab-pane label="全部" data-val="" name="all"></el-tab-pane>
		 		 	<el-tab-pane label="用户基础信息" name="basic-info" data-val="1"></el-tab-pane>
		 		 	<el-tab-pane label="用户特性信息" name="user-feature" data-val="3"></el-tab-pane>
		 		 	<el-tab-pane label="会议需求分析" name="recommends" data-val="2"></el-tab-pane>
		 		 	<el-tab-pane label="客户公司关系" name="requirements" data-val="4"></el-tab-pane>
		  	</el-tabs>
			</el-col>
			<el-col :span="10" style="text-align:right;padding-top:30px;font-size:14px">
				<a href="/foreground/mnglist?source=fieldData" style="color:#409EFF">查看客户字段明细</a>
			</el-col>
		</el-row>


		<el-table
		 :data="fieldData"
		 >
		 <el-table-column
			 prop="table"
			 label="字段分类"
			 :show-overflow-tooltip=true
			 width="110">
		 </el-table-column>
		 <el-table-column
			 prop="field_name"
			 label="字段名称"
			 width="100"
			>
		 </el-table-column>

		 <el-table-column
			 prop="name"
			 width="180"
			 label="完成柱状图">
			 <template scope="scope">
					<el-progress   :show-text=false	 :percentage="scope.row.field_perfection_degree"></el-progress>

			</template>
		 </el-table-column>

		 <el-table-column
			 prop="field_perfection_degree"
			 label="完善度"
			 sortable
			 :formatter="formatter">
		 </el-table-column>
		 <el-table-column
			 prop="customer_has_count"
			 label="有该字段的客户数"
			 width="200"
			 sortable>
		 </el-table-column>
		 <el-table-column
			 prop="customer_count"
			 label="已创建客户数"
			 width="160"
			 sortable>
		 </el-table-column>
		 <el-table-column
			 prop="diff_count"
			 label="不同版本数"
			 sortable>
		 </el-table-column>
		 <el-table-column
			 prop="agree_count"
			 label="同意次数"
			 sortable>
		 </el-table-column>
		 <el-table-column
			 prop="field_count"
			 label="字段创建及修改次数"
			 width="200"
			 sortable>
		 </el-table-column>
	 </el-table>



	 </div>

	</div>
</template>

<script>

export default {

	data: function () {
		return {
				listParam:{},
				loading: true,
				activeType: 'all',
				fieldData: [],
				createdTotal:0, // 已创建客户总数
				fieldTotal:0 // 总字段数

		}
	},
	mounted(){
		var self = this;
		self.loading = true;
		if(self.$route.query.source == 'mnglist') {

			self.$emit('changeNav')


		}
		self.getList();
	},
	methods: {

		handleClick(tab, event) {

      var self = this;
			self.listParam['type'] = tab.$attrs['data-val'];
			self.loading = true;
			self.getList();

    },


		getList() {
			var self = this;
			self.$ajax.get(self.$api.getModuleHost('CRM', "/manage/getFieldInfoList"),self.listParam).then(function (data) {
				self.fieldData = data.data.field_info;
				self.fieldTotal = data.data.customer_all_field_count
				self.createdTotal = data.data.customer_count
				self.loading = false;
			})
		},
		formatter(row, column) {
			 return row.field_perfection_degree+"%";
		 },


	},
	components: {

	}
}
</script>

<style lang="less" >
	.container{
		min-width:1366px;
		overflow-x:auto;
	}
	.city-select-txt{
		font-size:14px;
		margin:10px 0;
	}
	.table-title{
		cursor:pointer;
	}
	.data-layer{
		width:100%;
		height:100%;
		background:rgba(0, 0, 0, .5);
		position:fixed;
		top:0;
		left:0;
		z-index:10;
		text-align:center;

		.data-content{
			position:absolute;
			width:1100px;
			height:600px;
			background:#fff;
			text-align:left;
			top:50%;
			left:50%;
			margin-top:-300px;
			margin-left:-550px;
			border-radius:3px;
			.data-content-wrap{
				overflow-x:auto;
			}
			.change-chart{
				position:absolute;
				top:50%;
				width:50px;
				height:60px;
				background:rgba(0,0,0,.5);
				color:#fff;
				z-index:10;
				font-size:20px;
				text-align:center;
				line-height:60px;
				margin-top:-30px;
				cursor:pointer;
				&:hover{
					background:rgba(0,0,0,.4);
				}
			}
			.change-chart-left{
				left:0;
			}
			.change-chart-right{
				right:0;
			}
			.data-inner-content{
				height:500px;
				padding-top:100px;
				text-align:center;
				line-height:500px;
				font-size:14px;
			}
			.agent-info{
				font-size:14px;
				position:absolute;
				top:20px;
				left:20px;
				color:#333;
				span{
					display:inline-block;
					margin-right:40px;
					z-index:20;
					i{
						color:#20a0ff;
					}
				}
			}
			.legend-wrap{
				position:absolute;
				top:70px;
				left:390px;
				text-align:center;
				font-size:14px;
				span{
					display:inline-block;
					width:30px;
					height:15px;
					border-radius:3px;

					vertical-align:bottom;
					&:first-child{
						background:#F7A35C;
					}
					&:last-child{
						background:#90ED7D;
					}
				}
			}
		}
	}

</style>



